$templateCache.put("app/pages/about/about.html","<div class=\"about-page\"><div class=\"about-banner\" img-resize=\"\" m-size=\"{width: 750, height: 320}\"><div class=\"about-banner__text--mobile\"><h3 class=\"section-title\">校长寄语 <small>PRESIDENT\'S MESSAGE</small></h3></div></div><section class=\"about-section\"><div class=\"about-section__title\"><h3 class=\"section-title section-title--border\"><span>校长寄语</span> <small>PRESIDENT\'S MESSAGE</small></h3></div><div class=\"about-section__content\"><p>能教英文的教师很多，能把英文教出思维水平，并打开学生各个领域知识面的，却实在鲜见。</p><p>在我们的课堂上，每一分钟都是老师们多年教学经验的释放，是大家挑灯夜战的细致设计，是对学生个性与兴趣点的反复揣摩。与我一同共事的，都是志同道合的朋友，他们不仅只有教师这一个身份，走出课堂，他们是博士，是研究员，是社会各领域的资深精英，且都有丰富的海外经历。他们在授课时，所传递的，不止是英文教学，还有各式各样的新鲜知识，帮助学生完成知识储备的搭建。</p><p>对于现今普遍存在的走捷径教学法，类似于“不认识单词一样能解对题”，其实这种引导极不理性，极其片面，与其让学生费劲地去锻炼绕开单词的解题能力，不如要他把单词记下，全面地去解构题意，这才是语言学习的初衷。</p><p>浮躁的时代里，越来越多的语培机构把自己的推动力定义为“噱头性教育”，鼓励“发疯”或“走捷径”，大街小巷都叫嚣着“用美国人的思维学英语”，以此营造着各种话题性。</p><p>我们没有跟风，而是打出了一套自己的拳法，“外国思维学英语，也就是个噱头，理论上可行，却没有实际操作性。你的培养对象首先就不是美国人，你如何要求他有美国思维？在美国，一个华尔街的精英与大街上的流浪汉对英语的认识不会相同。在孩子5岁之前可以用这样的方式培养，但年龄大了之后，就不再适合，思维是建立在词汇和阅读上的自然形成，非刻意而为。如若不然，学习文言文时，又何必转换成白话文，还将一些晦涩难懂的词加上注解呢。干脆培养“古人思维”好了。</p><p>我们是一群匠人，舍得花费长时间大力气去打磨学生，探寻着学生身上隐藏的闪光点，以寻找出更为适合个人的教学方式。</p><p>也许有一天，我们会骑着想象，带上我的伙伴们，把学校造在山上，安在海边。</p><div class=\"about-section__content__contact\"><p><i class=\"iconfont icon-email-circle\"></i>tangying@uplusway.com（Sherry唐老师）</p><p><i class=\"iconfont icon-phone-circle\"></i>客服热线：028-84543367</p><p>有任何问题请随时联系我们</p></div><div class=\"about-section__content__signature\"><p><span class=\"signature-logo\"></span></p><p class=\"signature-name\">优加留学校长：Alex</p></div></div></section></div>");
